<template>
  <div class="home">
    <!-- 头部 -->
    <Header />
    <!-- tab切换 -->
    <TabPannel @activeTabFun="activeTabFun" />
    <Authority v-if="active == 0" />
    <Role v-else />
    <!-- 密码按钮 -->
    <div class="btn-box" v-drag>
      <i></i>
    </div>
    <!-- 密码生成 -->
    <el-dialog title="密码生成" :before-close="handleClose" :visible.sync="passwordDialog" width="830px" append-to-body :close-on-click-modal="false">
      <GeneratePassword ref="GeneratePassword" />
    </el-dialog>
  </div>
</template>

<script>
import Header from "../../components/header/index.vue";
import TabPannel from "../../components/tabPannel/index.vue";
// 权限
import Authority from "../authority/index.vue";
// 角色
import Role from "../role/index.vue";
//生成密码
import GeneratePassword from "../password/index.vue";
export default {
  components: {
    Header,
    TabPannel,
    Authority,
    Role,
    GeneratePassword,
  },
  data() {
    return {
      active: 0,
      passwordDialog: false,
    };
  },
  mounted() {},
  methods: {
    activeTabFun(index) {
      this.active = index;
    },
    handleClose() {
      this.$refs["GeneratePassword"].password = "";
      this.$refs["GeneratePassword"].newPassword = "";
      this.passwordDialog = false;
    },
    showScreenInteraction() {
      this.passwordDialog = true;
    },
  },
};
</script>

<style lang="scss" scoped>
.home {
  width: 100%;
  height: 100%;
  // background: #2b315c;
  background: #171b22;
  border-radius: 3px;
  // padding: 0 50px 30px 50px;
  box-sizing: border-box;
  // 头部
  .password-box {
    width: 80%;
    height: 80%;
    left: 10%;
    position: absolute;
    top: 10%;
    background: #171b22;
    z-index: 9999;
  }
  .btn-box {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background: linear-gradient(-90deg, #12b3ff 0, #0548a5 100%);
    position: absolute;
    top: 90%;
    z-index: 999;
    left: 0px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 10px;
    box-sizing: content-box;
    cursor: pointer;
    i {
      width: 35px;
      height: 35px;
      display: inline-block;
      background: url("../../assets/icons/pwd.png") no-repeat center;
      background-size: 100% 100%;
    }
  }
}
</style>
