import Vue from "vue";
Vue.directive("drag", {
  inserted(el, binding, vnode) {
    let oDiv = el;
    let mDiv = el;
    mDiv.onmousedown = function (e) {
      document.onselectstart = function () {
        return false;
      }; //解决拖动会选中文字的问题
      document.ondragstart = function () {
        return false;
      };
      // 鼠标按下，计算当前元素距离可视区的距离
      let distanceX = e.clientX - oDiv.offsetLeft;
      let distanceY = e.clientY - oDiv.offsetTop;
      oDiv.style.cursor = "move";
      debugger;
      document.onmousemove = function (ev) {
        oDiv.style.cursor = "move";
        let oevent = ev || e;
        oDiv.style.left = oevent.clientX - distanceX + "px";
        oDiv.style.top = oevent.clientY - distanceY + "px";
      };
      // 松开事件后，移除事件
      document.onmouseup = function (e2) {
        document.onselectstart = null;
        document.ondragstart = null;
        document.onmousemove = null;
        document.onmouseup = null;
        oDiv.style.cursor = "pointer";
        if (e.clientX == e2.clientX && e.clientY == e2.clientY) {
          //点击拖拽冲突
          vnode.context.showScreenInteraction();
        }
      };
    };
  },
});
