      <ul>
        <li class="role-li" style="position: relative" v-for="(item, index) in roleOptions" :key="item.id.toString()" @click="editRoleFun($event, item)" @mousedown="createRipple($event, this)" @mouseup="removeRipple($event, this)">
          <div :class="[
              'left-div',
              'leftIn',
              {
                active: index % 7 == 0,
