                active5: index % 7 == 5,
                active6: index % 7 == 6,
                active7: index % 7 == 7,
              },
            ]">
            <span></span>
          </div>
          <div class="font">{{ item.roleName }}</div>
        </li>
      </ul>
    </div>
    <el-dialog :title="title" :visible.sync="editRole" width="600px" append-to-body top="6vh">
      <div style="display: flex">
        <div class="search-input search-margin">
          <el-input v-model="keywords2" placeholder="请输入权限名称或URL路径搜索" clearable @keyup.enter.native="searchInputFun2" />
        </div>

        <div class="search-btn-input" @click="searchInputFun2">
          <i class="el-icon-search"></i>搜索
        </div>
      </div>
      <ul class="role-ul">
        <li v-for="item in tableData2" :key="item.id.toString()" style="position: relative" :class="{
            active:
              editRoleForm.permissionInfos.indexOf(item.id.toString()) != -1,
          }" @click="changeRolePermiss(item)" @mousedown="createRipple($event, this)" @mouseup="removeRipple($event, this)" v-show="item.open == 0">
          <span class="span-img"></span>
          <div class="font-desc">
            <div class="font-desc-name">{{ item.urlName }}</div>
            <div class="font-desc-url">{{ item.url }}</div>
          </div>
        </li>
      </ul>
      <div class="mini-pagination">
        <el-pagination @size-change="handleSizeChange2" @current-change="handleCurrentChange2" :current-page.sync="pageNum2" background :page-size="pageSize2" layout="total, prev, pager, next" :total="total">
        </el-pagination>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="editRoleSubmit" style="background: #0548a5;border-color:#0548a5;">确 定</el-button>
        <el-button @click="beforeClose">取 消</el-button>
      </div>
