</template>

<script>
// 头部
import Header from "../../components/header/index.vue";
// tab切换
import TabPannel from "../../components/tabPannel/index.vue";

export default {
  components: {
    Header,
    TabPannel,
  },
  data() {
    return {
      roleTabShow: "-350",
      jsId: "",
      file: null,
      questionObject: {
        // questionsNmae: '',
        file: null,
      },
      questionIds: [],
      formLabelWidth: "120px",
      fileList: [{ name: undefined }],
      templateShow: false,
      username: "",
      tel: "",
      dataStatus: "",
      pageSize: 10,
      pageNum: 0,
      pageSize2: 10,
      pageNum2: 0,
      total: 20,
      time: ["", ""],
      tableData: [],
      tableData2: [],
      multipleSelection: [],
      switchStatus: "0",
      currentPage: 5,
      add: false,
      // 表单校验
      addrules: {
        urlName: [
          { required: true, message: "权限名称不能为空", trigger: "blur" },
        ],
        url: [{ required: true, message: "URL路径不能为空", trigger: "blur" }],
      },
      addForm: {
        urlName: "",
        url: "",
        open: "0",
        description: "",
        roleInfos: [],
      },
      title: "角色授权",
      // 部门树选项
      deptOptions: undefined,
      // 性别状态字典
      //修改
      // 状态数据字典
      statusOptions: [],
      // 岗位选项
      postOptions: [],
      // 角色选项
      roleOptions: [],
      edit: false,
      editrules: {
        urlName: [
          { required: true, message: "权限名称不能为空", trigger: "blur" },
        ],
        url: [{ required: true, message: "URL路径不能为空", trigger: "blur" }],
      },
      editForm: {
        urlName: "",
        url: "",
        open: "0",
        description: "",
        roleInfos: [],
        id: "",
      },
      editRoleForm: {
        permissionInfos: [],
      },

      resetFlag: false,
      resetForm: {
        password: "",
      },
      editRole: false,
      activeRole: {},
      dictOptions: [],
      keywords: "",
      keywords2: "",
    };
  },
  mounted() {
    // this.canvas();
    // this.searchFn()
    this.searchFn2();
    this.getRoles();
    this.queryDictByDictTypeId();
  },
  methods: {
    // canvas(){
    //   beginLine('myCanvas')
    // },
    setStyle(index) {
      if (index < 10) {
        return { "animation-delay": "0." + index + "s" };
      } else {
        return { "animation-delay": index * 0.1 + "s" };
      }
    },
    handleClose() {
      this.$refs["form"].resetFields();
      this.add = false;
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    //点击行触发，选中或不选中复选框
    handleRowClick(row) {
      this.$refs.multipleTable.toggleRowSelection(row);
      // 获取当前选中的数据
      const _selectData = this.$refs.multipleTable.selection;
      console.log("当前选中的数据", _selectData);
      this.handleSelectionChange(_selectData);
    },
    // changeOpen (val) {
    //   if (this.addForm.open == '0') {
    //     if (val == 'addForm') {
    //       this.addForm.roleInfos = []
    //     } else {
    //       this.editForm.roleInfos = []
    //     }
    //   }

    // },
    beforeClose() {
      this.editRole = false;
      this.pageNum2 = 1;
      this.qxName2 = "";
      this.qxUrl2 = "";
      this.searchFn2();
    },
    changeRolePermiss(item) {
      if (this.editRoleForm.permissionInfos.indexOf(item.id.toString()) == -1) {
        this.editRoleForm.permissionInfos.push(item.id.toString());
      } else {
        this.editRoleForm.permissionInfos.splice(
          this.editRoleForm.permissionInfos.indexOf(item.id.toString()),
          1
        );
      }
    },
    logout() {
      this.$post($url.DATA_URL + "/logout").then((res) => {
        if (res.data.code == 0) {
          this.$router.push("/");
        } else {
          this.$message({
            type: "error",
            message: res.data.msg,
          });
        }
      });
    },
    queryDictByDictTypeId() {
      this.$get(
        $url.DATA_URL + "/gateway/queryDictByDictTypeId?dictTypeId=1"
      ).then((res) => {
        if (res.data.code == 0) {
          this.dictOptions = res.data.data;
        } else {
          this.$router.push("/");
          // this.$message({
          //   type: "error",
          //   message: res.data.msg
          // });
        }
      });
    },
    queryPermissionByRoleId() {
      this.$get(
        $url.DATA_URL +
          "/gateway/queryPermissionsByRoleId?roleId=" +
          this.activeRole.id
      ).then((res) => {
        if (res.data.code == 0) {
          this.editRoleForm.permissionInfos = [];
          for (let i = 0; i < res.data.data.length; i++) {
            this.editRoleForm.permissionInfos.push(
              res.data.data[i].id.toString()
            );
          }
        } else {
          this.$message({
            type: "error",
            message: res.data.msg,
          });
        }
      });
    },
    editRoleFun(e, item) {
      this.activeRole = item;
      this.editRole = true;
      this.queryPermissionByRoleId();
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleEdit(row) {
      this.title = "修改权限";
      this.add = true;
      this.addForm.urlName = row.urlName;
      this.addForm.open = row.open.toString();
      this.addForm.url = row.url;
      this.addForm.description = row.description;
      this.addForm.roleInfos = [];
      for (let i = 0; i < row.roleInfos.length; i++) {
        this.addForm.roleInfos.push(row.roleInfos[i].id);
      }
      this.addForm.id = row.id;
    },

    handleSizeChange(val) {
      this.pageSize = val;
      // this.getUserList()
      this.searchFn();
    },
    handleCurrentChange(val) {
      this.pageNum = val;
      // this.getUserList()
      this.searchFn();
    },
    handleSizeChange2(val) {
      this.pageSize2 = val;
      // this.getUserList()
      this.searchFn2();
    },
    handleCurrentChange2(val) {
      this.pageNum2 = val;
      // this.getUserList()
      this.searchFn2();
    },
    handleSelectChange() {},
    addDialog() {
      this.addForm = {
        urlName: "",
        url: "",
        open: "0",
        description: "",
        roleInfos: [],
      };
      this.title = "新增权限";
      this.add = true;
      //this.getTreeselect()
    },
    getRoles() {
      this.$get($url.DATA_URL + "/gateway/queryAllRoles").then((res) => {
        if (res.data.code == 0) {
          this.roleOptions = res.data.data;
        } else {
          this.$router.push("/login");
          // this.$message({
          //   type: "error",
          //   message: res.data.msg
          // });
        }
      });
    },
    //新增确认按钮
    addSubmit() {
      let url = "/gateway/addPermission";
      if (this.addForm.id) {
        url = "/gateway/updatePermission";
      }
      this.$refs["form"].validate((valid) => {
        if (valid) {
          if (this.addForm.open == "0" && this.addForm.roleInfos.length == 0) {
            this.$message.warning("请选择角色!");
            return;
          }
          // let params = {
          //   urlName: this.addForm.urlName,
          //   url: this.addForm.url,
          //   open: this.addForm.open,
          //   description: this.addForm.description,
          //   roleInfos: this.addForm.roleInfos
          // }
          let roleArr = [];
          for (let i = 0; i < this.roleOptions.length; i++) {
            if (this.addForm.roleInfos.indexOf(this.roleOptions[i].id) != -1) {
              roleArr.push(this.roleOptions[i]);
            }
          }
          this.addForm.roleInfos = roleArr;
          this.$post($url.DATA_URL + url, this.addForm).then((res) => {
            if (res.data.code == 0) {
              this.add = false;
              this.$message({
                type: "success",
                message: res.data.msg,
              });
              // this.getUserList();
              this.searchFn();
            } else {
              this.$message({
                type: "error",
                message: res.data.msg,
              });
            }
          });
        } else {
          this.add = true;
        }
      });
    },
    addCancel() {
      this.$refs["form"].resetFields();
      this.add = false;
    },
    // 编辑弹窗
    editDialog() {
      console.log(this.multipleSelection, "editDialog.multipleSelection");
      this.editForm = {
        urlName: "",
        url: "",
        open: "1",
        description: "",
        roleInfos: [],
        id: "",
      };
      if (
        this.multipleSelection.length > 1 ||
        this.multipleSelection.length < 1
      ) {
        return this.$message({
          type: "error",
          message: "请选择一条权限信息",
        });
      } else {
        this.title = "修改权限";
        this.edit = true;
        this.editForm.urlName = row.urlName;
        this.editForm.open = row.open.toString();
        this.editForm.url = row.url;
        this.editForm.description = row.description;
        this.editForm.roleInfos = row.roleInfos;
        this.editForm.id = row.id;
      }
    },
    //修改确认按钮
    editSubmit() {
      let params = {
        urlName: this.editForm.urlName,
        open: this.editForm.open,
        url: this.editForm.url,
        description: this.editForm.description,
        roleInfos: this.editForm.roleInfos,
        id: this.editForm.id,
      };
      let roleArr = [];
      for (let i = 0; i < this.roleOptions.length; i++) {
        if (params.roleInfos.indexOf(this.roleOptions[i].id) != -1) {
          roleArr.push(this.roleOptions[i]);
        }
      }
      params.roleInfos = roleArr;
      this.$post($url.DATA_URL + "/gateway/updatePermission", params).then(
        (res) => {
          if (res.data.code == 0) {
            this.$message({
              type: "success",
              message: res.data.msg,
            });
            this.searchFn();
          } else {
            this.$message({
              type: "error",
              message: res.data.msg,
            });
          }
        }
      );
      this.edit = false;
    },
    editRoleSubmit() {
      let params = {
        permissionInfos: [],
        id: this.activeRole.id,
      };
      for (let i = 0; i < this.editRoleForm.permissionInfos.length; i++) {
        params.permissionInfos.push({
          id: this.editRoleForm.permissionInfos[i],
        });
      }
      this.$post($url.DATA_URL + "/gateway/bindPermissionByRole", params).then(
        (res) => {
          if (res.data.code == 0) {
            this.$message({
              type: "success",
              message: res.data.msg,
            });
            this.searchFn();
          } else {
            this.$message({
              type: "error",
              message: res.data.msg,
            });
          }
        }
      );
      this.editRole = false;
      this.pageNum2 = 1;
      this.handleSizeChange2();
      this.activeRole = {};
    },
    editCancel() {
      this.edit = false;
    },

    handleDel(row) {
      this.$confirm("此操作将永久删除该条权限信息, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        this.$get($url.DATA_URL + "/gateway/delPermission?id=" + row.id).then(
          (res) => {
            if (res.data.code == 0) {
              this.$message({
                type: "success",
                message: "删除成功!",
              });
              this.searchFn();
            } else {
              this.$message({
                type: "error",
                message: res.data.msg,
              });
            }
          }
        );
      });
      // this.$delete( `/onlineteaching/user/${row.id}`).then((res) => {
      //   this.getUserList()
      // })
    },
    delDialog() {
      if (this.multipleSelection.length < 1) {
        return this.$message({
          type: "error",
          message: "请至少选择一条数据",
        });
      }
      let that = this;
      this.$confirm("此操作将永久删除数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        let ids = "";
        for (let i = 0; i < that.multipleSelection.length; i++) {
          ids += that.multipleSelection[i].id.toString() + ",";
        }
        this.$get($url.DATA_URL + "/gateway/delPermissions?ids=" + ids).then(
          (res) => {
            if (res.data.code == 0) {
              this.$message({
                type: "success",
                message: "删除成功!",
              });
              this.searchFn();
            } else {
              this.$message({
                type: "error",
                message: res.data.msg,
              });
            }
          }
        );
      });
    },
    uploadFn() {
      this.templateShow = true;
    },
    uploadChange(file) {
      this.questionObject.file = file.raw;
    },
    uploadRemove() {
      this.file = null;
    },
    // 转换
    formatter(params) {
      let paramsArr = params.split("&");
      let paramsStr = "";
      for (let item in paramsArr) {
        paramsStr += encodeURIComponent(paramsArr[item].split("=")[0]) + "=";
        paramsStr += encodeURIComponent(paramsArr[item].split("=")[1]) + "&";
      }
      return paramsStr;
    },
    searchInputFun() {
      this.pageNum = 1;
      this.searchFn();
    },
    searchInputFun2() {
      this.pageNum2 = 1;
      this.searchFn2();
    },
    searchFn() {
      if (!this.cookie.getCookie("username")) {
        this.$router.push("/login");
      }
      let params = {
        keywords: this.keywords,
        permissionInfoPage: {
          current: this.pageNum,
          size: this.pageSize,
        },
      };
      this.$post($url.DATA_URL + "/gateway/queryPermission", params).then(
        (res) => {
          if (res.data.code === 0) {
            this.tableData = res.data.data.details;
            this.total = res.data.data.total;
          } else {
            this.$message({
              type: "error",
              message: res.data.msg,
            });
            this.$router.push("/login");
          }
        }
      );
    },
    searchFn2() {
      if (!this.cookie.getCookie("username")) {
        this.$router.push("/login");
      }
      let params = {
        keywords: this.keywords2,
        permissionInfoPage: {
          current: this.pageNum2,
          size: this.pageSize2,
        },
      };
      this.$post($url.DATA_URL + "/gateway/queryPermission", params).then(
        (res) => {
          if (res.data.code === 0) {
            this.tableData2 = res.data.data.details;
            this.total = res.data.data.total;
          } else {
            this.$router.push("/login");
            // this.$message({
            //   type: 'error',
            //   message: res.data.msg
            // })
          }
        }
      );
    },
    resetFn() {
      this.username = "";
      this.tel = "";
      this.dataStatus = "";
      this.time = [];
      this.searchFn();
    },
    //按钮水波纹
    computeRippleStyles(element, event) {
      const { top, left } = element.getBoundingClientRect();
      const { clientWidth, clientHeight } = element;

      const radius = Math.sqrt(clientWidth ** 2 + clientHeight ** 2) / 2;
      const size = radius * 2;

      const localX = event.clientX - left;
      const localY = event.clientY - top;

      const centerX = (clientWidth - radius * 2) / 2;
      const centerY = (clientHeight - radius * 2) / 2;

      const x = localX - radius;
      const y = localY - radius;

      return { x, y, centerX, centerY, size };
    },
    createRipple(event) {
      const container = event.currentTarget;
      const { x, y, centerX, centerY, size } = this.computeRippleStyles(
        container,
        event
      );
      const ripple = document.createElement("div");
      ripple.classList.add("myripple");
      ripple.style.opacity = `0`;
      ripple.style.transform = `translate(${x}px, ${y}px) scale3d(.3, .3, .3)`;
      ripple.style.width = `${size}px`;
      ripple.style.height = `${size}px`;
      ripple.style.position = `absolute`;
      ripple.style.top = `0`;
      ripple.style.left = `0`;
      ripple.style["z-index"] = `100`;
      ripple.style["border-radius"] = `50%`;
      ripple.style["background-color"] = "#fad4a1"; //`currentColor`
      ripple.style["opacity"] = `0`;
      ripple.style.transition =
        "transform 0.2s cubic-bezier(0.68, 0.01, 0.62, 0.6)," +
        "opacity 0.08s linear";
      ripple.style["will-change"] = "transform, opacity";
      ripple.style["pointer-events"] = "none";
      // 记录水波的创建时间
      ripple.dataset.createdAt = String(performance.now());

      const { position } = window.getComputedStyle(container);
      container.style.overflow = "hidden";
      position === "static" && (this.style.position = "relative");

      container.appendChild(ripple);

      window.setTimeout(() => {
        ripple.style.transform = `translate(${centerX}px, ${centerY}px) scale3d(1, 1, 1)`;
        ripple.style.opacity = `.25`;
      });
    },
    removeRipple(event) {
      const container = event.currentTarget;
      const ripples = container.querySelectorAll(".myripple");
      if (!ripples.length) {
        return;
      }

      const lastRipple = ripples[ripples.length - 1];
      // 通过水波的创建时间计算出扩散动画还需要执行多久，确保每一个水波都完整的执行了扩散动画
      const delay =
        300 - performance.now() + Number(lastRipple.dataset.createdAt);

      setTimeout(() => {
        lastRipple.style.opacity = `0`;

        setTimeout(() => lastRipple.parentNode?.removeChild(lastRipple), 300);
      }, delay);
    },
  },
};
</script>

