<template>
  <!-- 权限管理 -->
  <div class="authority">
    <!-- 内容 -->
    <div class="table-pannel">
      <!-- 操作栏 -->
      <div class="btn-box">
        <!-- 按钮 -->
        <div style="display: flex">
          <div class="add-btn" @click="addDialog">
            <div class="add-btn-content">
              <div class="add-btn-bg"></div>
              <div class="add-btn-name">新增</div>
            </div>
          </div>
          <div :class="['edit-btn',{'edit-btn-grow':multipleSelection.length > 0}]" @click="delDialog">
            <div class="add-btn-content">
              <div class="del-btn-bg"></div>
              <div class="edit-btn-name">删除</div>
            </div>
          </div>
        </div>
        <!-- 搜索 -->
        <div class="border-box-search">
          <div style="display: flex;transform: skewX(40deg);margin-top: 5px;z-index:999;">
            <div class="search-input">
              <el-input v-model="keywords" placeholder="请输入URL路径或权限名称搜索" clearable @keyup.enter.native="searchInputFun" />
            </div>
            <div class="search-btn-input" @click="searchInputFun">
              <i class="el-icon-search"></i>
              搜 索
              <!-- <div class="search-btn-bg"></div> -->
            </div>
          </div>
        </div>

      </div>
      <!-- 表格数据 -->
      <div class="flex-list">
        <div class="main-dataset animated flip" v-for="(item,index) in tableData" :key="item.id" :style="setStyle(index)">

          <div class="dataset-operate">
            <div class="dataset-edit" @click.stop="handleEdit(item)" v-show="item.fixed == 0">
              <i class="el-icon-edit"></i>
            </div>
            <div class="dataset-delete" @click.stop="handleDel(item)" v-show="item.fixed == 0">
              <i class="el-icon-delete" style="padding-left: 16px"></i>
            </div>
            <div class="dataset-delete" @click.stop="handleDetail(item)">
              <i class="el-icon-document" style="padding-left: 16px"></i>
            </div>
          </div>
          <div class="dataset-info ellipsis">
            <!-- @click.stop="activeAuthor($event,item)" -->
            <div class="info-name ellipsis">{{ item.urlName }}</div>
            <div class="info-url" :title="item.url">{{ item.url }}</div>
            <div class="info-open">
              <template>
                <span v-if="item.open == '0'"> 私有 </span>
                <span v-else-if="item.open == '1'"> 公开 </span>
                <span v-else> 匿名 </span>
              </template>
            </div>
          </div>
          <div class="dataset-type">
            <span class="dataset-type-select" v-show="item.fixed == 0" @click.stop="selectData($event,item)"></span>
          </div>
        </div>
      </div>

      <div class="pagination">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="pageNum" background :page-size="pageSize" layout="total, prev, pager, next" :total="total">
        </el-pagination>
      </div>
    </div>

    <!-- 添加 修改 -->
    <el-dialog :title="title" :visible.sync="add" :before-close="handleClose" width="600px" append-to-body>
      <el-form ref="form" :model="addForm" :rules="addrules" label-width="80px">
        <el-row>
          <el-col :span="12">
            <el-form-item label="权限名称" prop="urlName">
              <el-input v-model="addForm.urlName" :disabled="!edit" placeholder="请输入权限名称" clearable />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="URL路径" prop="url">
              <el-input v-model="addForm.url" :disabled="!edit" placeholder="请输入URL路径" clearable />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row class="special-row">
          <el-col :span="24">
            <el-form-item label="是否公开" style="margin-bottom: 5px">
              <el-select v-model="addForm.open" :disabled="!edit" placeholder="请选择是否公开" clearable style="width: 100%">
                <el-option v-for="item in dictOptions" :key="item.id.toString()" :label="item.dictName" :value="item.dictVal"></el-option>
              </el-select>
            </el-form-item>
            <ul class="desc-list">
              <li>1、私有：登陆后需要为角色分配权限</li>
              <li>2、公开：登陆后无需为角色分配权限</li>
              <li>3、匿名：无需登陆</li>
            </ul>
          </el-col>
        </el-row>
        <el-row> </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="角色选择">
              <el-select v-model="addForm.roleInfos" placeholder="请选择角色" multiple="" :disabled="addForm.open == '1' || addForm.open == '2' || !edit" clearable style="width: 100%">
                <el-option v-for="item in roleOptions" :key="item.id.toString()" :label="item.roleName" :value="item.id.toString()"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="描述">
              <el-input v-model="addForm.description" :disabled="!edit" type="textarea" placeholder="请输入内容"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer" v-show="edit">
        <el-button type="primary" @click="addSubmit" style="background: #0548a5;border-color:#0548a5;">确 定</el-button>
        <el-button @click="addCancel">取 消</el-button>
      </div>
    </el-dialog>
    <!-- 角色 -->
    <div class="role-manage" :style="'right:' + roleTabShow + 'px'">
      <div class="box-title">
        <div class="box-title-font">角色</div>
        <div class="close-btn" @click="roleTabShow = '-350'"></div>

      </div>
      <!-- { leftOut: (activeRole.id = item.id) }, -->
      <ul>
        <li class="role-li" style="position: relative" v-for="(item, index) in roleOptions" :key="item.id.toString()" @click="editRoleFun($event, item)" @mousedown="createRipple($event, this)" @mouseup="removeRipple($event, this)">
          <div :class="[
              'left-div',
              'leftIn',
              {
                active: index % 7 == 0,
                active1: index % 7 == 1,
                active2: index % 7 == 2,
                active3: index % 7 == 3,
                active4: index % 7 == 4,
                active5: index % 7 == 5,
                active6: index % 7 == 6,
                active7: index % 7 == 7,
              },
            ]">
            <span></span>
          </div>
          <div class="font">{{ item.roleName }}</div>
        </li>
      </ul>
    </div>
    <el-dialog :title="title" :visible.sync="editRole" width="600px" append-to-body>
      <div style="display: flex">
        <div class="search-input search-margin">
          <!-- <div class="search-name">权限名称：</div> -->
          <el-input v-model="keywords2" placeholder="请输入权限名称或URL路径搜索" clearable @keyup.enter.native="searchInputFun2" />
        </div>

        <div class="search-btn-input" @click="searchInputFun2">
          <i class="el-icon-search"></i>搜索
        </div>
      </div>
      <ul class="role-ul">
        <li v-for="item in tableData2" :key="item.id.toString()" style="position: relative" :class="{
            active:
              editRoleForm.permissionInfos.indexOf(item.id.toString()) != -1,
          }" @click="changeRolePermiss(item)" @mousedown="createRipple($event, this)" @mouseup="removeRipple($event, this)" v-show="item.open == 0">
          <span class="span-img"></span>
          <div class="font-desc">
            <div class="font-desc-name">{{ item.urlName }}</div>
            <div class="font-desc-url">{{ item.url }}</div>
          </div>
        </li>
      </ul>
      <div class="mini-pagination">
        <el-pagination @size-change="handleSizeChange2" @current-change="handleCurrentChange2" :current-page.sync="pageNum2" background :page-size="pageSize2" layout="total, prev, pager, next" :total="total">
        </el-pagination>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="editRoleSubmit" style="background: #0548a5">确 定</el-button>
        <el-button @click="beforeClose">取 消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
// 头部
import Header from "../../components/header/index.vue";
// tab切换
import TabPannel from "../../components/tabPannel/index.vue";
// import {beginLine} from '../../plugins/canvasLine.js';
export default {
  components: {
    Header,
    TabPannel,
  },
  data() {
    return {
      roleTabShow: "-350",
      jsId: "",
      file: null,
      questionObject: {
        // questionsNmae: '',
        file: null,
      },
      questionIds: [],
      formLabelWidth: "120px",
      fileList: [{ name: undefined }],
      templateShow: false,
      username: "",
      tel: "",
      dataStatus: "",
      pageSize: 10,
      pageNum: 0,
      pageSize2: 10,
      pageNum2: 0,
      total: 20,
      time: ["", ""],
      tableData: [],
      tableData2: [],
      multipleSelection: [],
      switchStatus: "0",
      currentPage: 5,
      add: false,
      // 表单校验
      addrules: {
        urlName: [
          { required: true, message: "权限名称不能为空", trigger: "blur" },
        ],
        url: [{ required: true, message: "URL路径不能为空", trigger: "blur" }],
      },
      addForm: {
        urlName: "",
        url: "",
        open: "0",
        description: "",
        roleInfos: [],
      },
      title: "",
      // 部门树选项
      deptOptions: undefined,
      // 性别状态字典
      //修改
      // 状态数据字典
      statusOptions: [],
      // 岗位选项
      postOptions: [],
      // 角色选项
      roleOptions: [],
      edit: false,
      editrules: {
        urlName: [
          { required: true, message: "权限名称不能为空", trigger: "blur" },
        ],
        url: [{ required: true, message: "URL路径不能为空", trigger: "blur" }],
      },
      editForm: {
        urlName: "",
        url: "",
        open: "0",
        description: "",
        roleInfos: [],
        id: "",
      },
      editRoleForm: {
        permissionInfos: [],
      },

      resetFlag: false,
      resetForm: {
        password: "",
      },
      editRole: false,
      activeRole: {},
      dictOptions: [],
      keywords: "",
      keywords2: "",
      edit: true,
    };
  },
  mounted() {
    // this.canvas();
    this.searchFn();
    // this.searchFn2()
    this.getRoles();
    this.queryDictByDictTypeId();
  },
  methods: {
    selectData(e, data) {
      if (data.fixed != 0) {
        return;
      }
      let classNames = e.target.parentElement.className;
      if (classNames.indexOf("dataset-type-select") == -1) {
        e.target.parentElement.className = "dataset-type dataset-type-select";
        this.multipleSelection.push(data);
      } else {
        e.target.parentElement.className = "dataset-type";
        this.multipleSelection = this.multipleSelection.filter(function (item) {
          return item.id !== data.id;
        });
      }
    },
    activeAuthor(e, item) {
      let classNames = e.currentTarget.parentElement.className;
      if (classNames.indexOf("dataset-type-active") == -1) {
        e.currentTarget.parentElement.className =
          "main-dataset animated flip dataset-type-active";
      } else {
        e.currentTarget.parentElement.className = "main-dataset animated flip";
      }
    },
    canvas() {
      //beginLine('myCanvas')
    },
    setStyle(index) {
      if (index < 10) {
        return { "animation-delay": "0." + index + "s" };
      } else {
        return { "animation-delay": index * 0.1 + "s" };
      }
    },
    handleClose() {
      this.$refs["form"].resetFields();
      this.add = false;
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    //点击行触发，选中或不选中复选框
    handleRowClick(row) {
      this.$refs.multipleTable.toggleRowSelection(row);
      // 获取当前选中的数据
      const _selectData = this.$refs.multipleTable.selection;
      console.log("当前选中的数据", _selectData);
      this.handleSelectionChange(_selectData);
    },
    // changeOpen (val) {
    //   if (this.addForm.open == '0') {
    //     if (val == 'addForm') {
    //       this.addForm.roleInfos = []
    //     } else {
    //       this.editForm.roleInfos = []
    //     }
    //   }

    // },
    beforeClose() {
      this.editRole = false;
      this.pageNum2 = 1;
      this.qxName2 = "";
      this.qxUrl2 = "";
      this.searchFn2();
    },
    changeRolePermiss(item) {
      if (this.editRoleForm.permissionInfos.indexOf(item.id.toString()) == -1) {
        this.editRoleForm.permissionInfos.push(item.id.toString());
      } else {
        this.editRoleForm.permissionInfos.splice(
          this.editRoleForm.permissionInfos.indexOf(item.id.toString()),
          1
        );
      }
    },
    logout() {
      this.$post($url.DATA_URL + "/logout").then((res) => {
        if (res.data.code == 0) {
          this.$router.push("/");
        } else {
          this.$message({
            type: "error",
            message: res.data.msg,
          });
        }
      });
    },
    queryDictByDictTypeId() {
      this.$get(
        $url.DATA_URL + "/gateway/queryDictByDictTypeId?dictTypeId=1"
      ).then((res) => {
        if (res.data.code == 0) {
          this.dictOptions = res.data.data;
        } else {
          this.$router.push("/");
          // this.$message({
          //   type: "error",
          //   message: res.data.msg
          // });
        }
      });
    },
    queryPermissionByRoleId() {
      this.$get(
        $url.DATA_URL +
          "/gateway/queryPermissionsByRoleId?roleId=" +
          this.activeRole.id
      ).then((res) => {
        if (res.data.code == 0) {
          this.editRoleForm.permissionInfos = [];
          for (let i = 0; i < res.data.data.length; i++) {
            this.editRoleForm.permissionInfos.push(
              res.data.data[i].id.toString()
            );
          }
        } else {
          this.$message({
            type: "error",
            message: res.data.msg,
          });
        }
      });
    },
    editRoleFun(e, item) {
      this.activeRole = item;
      this.editRole = true;
      this.queryPermissionByRoleId();
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleEdit(row) {
      this.title = "修改权限";
      this.add = true;
      this.edit = true;
      this.addForm.urlName = row.urlName;
      this.addForm.open = row.open.toString();
      this.addForm.url = row.url;
      this.addForm.description = row.description;
      this.addForm.roleInfos = [];
      for (let i = 0; i < row.roleInfos.length; i++) {
        this.addForm.roleInfos.push(row.roleInfos[i].id);
      }
      this.addForm.id = row.id;
    },
    handleDetail(row) {
      this.title = "权限详情";
      this.add = true;
      this.edit = false;
      this.addForm.urlName = row.urlName;
      this.addForm.open = row.open.toString();
      this.addForm.url = row.url;
      this.addForm.description = row.description;
      this.addForm.roleInfos = [];
      for (let i = 0; i < row.roleInfos.length; i++) {
        this.addForm.roleInfos.push(row.roleInfos[i].id);
      }
      this.addForm.id = row.id;
    },

    handleSizeChange(val) {
      this.pageSize = val;
      // this.getUserList()
      this.searchFn();
    },
    handleCurrentChange(val) {
      this.pageNum = val;
      // this.getUserList()
      this.searchFn();
    },
    handleSizeChange2(val) {
      this.pageSize2 = val;
      // this.getUserList()
      this.searchFn2();
    },
    handleCurrentChange2(val) {
      this.pageNum2 = val;
      // this.getUserList()
      this.searchFn2();
    },
    handleSelectChange() {},
    addDialog() {
      this.addForm = {
        urlName: "",
        url: "",
        open: "0",
        description: "",
        roleInfos: [],
      };
      this.title = "新增权限";
      this.edit = true;
      this.add = true;
      //this.getTreeselect()
    },
    getRoles() {
      this.$get($url.DATA_URL + "/gateway/queryAllRoles").then((res) => {
        if (res.data.code == 0) {
          this.roleOptions = res.data.data;
        } else {
          this.$router.push("/login");
          // this.$message({
          //   type: "error",
          //   message: res.data.msg
          // });
        }
      });
    },
    //新增确认按钮
    addSubmit() {
      if (!this.edit) {
        return;
      }
      let url = "/gateway/addPermission";
      if (this.addForm.id) {
        url = "/gateway/updatePermission";
      }
      this.$refs["form"].validate((valid) => {
        if (valid) {
          if (this.addForm.open == "0" && this.addForm.roleInfos.length == 0) {
            this.$message.warning("请选择角色!");
            return;
          }
          // let params = {
          //   urlName: this.addForm.urlName,
          //   url: this.addForm.url,
          //   open: this.addForm.open,
          //   description: this.addForm.description,
          //   roleInfos: this.addForm.roleInfos
          // }
          let roleArr = [];
          for (let i = 0; i < this.roleOptions.length; i++) {
            if (this.addForm.roleInfos.indexOf(this.roleOptions[i].id) != -1) {
              roleArr.push(this.roleOptions[i]);
            }
          }
          this.addForm.roleInfos = roleArr;
          this.$post($url.DATA_URL + url, this.addForm).then((res) => {
            if (res.data.code == 0) {
              this.add = false;
              this.$message({
                type: "success",
                message: res.data.msg,
              });
              // this.getUserList();
              this.searchFn();
            } else {
              this.$message({
                type: "error",
                message: res.data.msg,
              });
            }
          });
        } else {
          this.add = true;
        }
      });
    },
    addCancel() {
      this.$refs["form"].resetFields();
      this.add = false;
    },
    // 编辑弹窗
    editDialog() {
      console.log(this.multipleSelection, "editDialog.multipleSelection");
      this.editForm = {
        urlName: "",
        url: "",
        open: "1",
        description: "",
        roleInfos: [],
        id: "",
      };
      if (
        this.multipleSelection.length > 1 ||
        this.multipleSelection.length < 1
      ) {
        return this.$message({
          type: "error",
          message: "请选择一条权限信息",
        });
      } else {
        this.title = "修改权限";
        this.edit = true;
        this.editForm.urlName = row.urlName;
        this.editForm.open = row.open.toString();
        this.editForm.url = row.url;
        this.editForm.description = row.description;
        this.editForm.roleInfos = row.roleInfos;
        this.editForm.id = row.id;
      }
    },
    //修改确认按钮
    editSubmit() {
      let params = {
        urlName: this.editForm.urlName,
        open: this.editForm.open,
        url: this.editForm.url,
        description: this.editForm.description,
        roleInfos: this.editForm.roleInfos,
        id: this.editForm.id,
      };
      let roleArr = [];
      for (let i = 0; i < this.roleOptions.length; i++) {
        if (params.roleInfos.indexOf(this.roleOptions[i].id) != -1) {
          roleArr.push(this.roleOptions[i]);
        }
      }
      params.roleInfos = roleArr;
      this.$post($url.DATA_URL + "/gateway/updatePermission", params).then(
        (res) => {
          if (res.data.code == 0) {
            this.$message({
              type: "success",
              message: res.data.msg,
            });
            this.searchFn();
          } else {
            this.$message({
              type: "error",
              message: res.data.msg,
            });
          }
        }
      );
      this.edit = false;
    },
    editRoleSubmit() {
      let params = {
        permissionInfos: [],
        id: this.activeRole.id,
      };
      for (let i = 0; i < this.editRoleForm.permissionInfos.length; i++) {
        params.permissionInfos.push({
          id: this.editRoleForm.permissionInfos[i],
        });
      }
      this.$post($url.DATA_URL + "/gateway/bindPermissionByRole", params).then(
        (res) => {
          if (res.data.code == 0) {
            this.$message({
              type: "success",
              message: res.data.msg,
            });
            this.searchFn();
          } else {
            this.$message({
              type: "error",
              message: res.data.msg,
            });
          }
        }
      );
      this.editRole = false;
      this.pageNum2 = 1;
      this.handleSizeChange2();
      this.activeRole = {};
    },
    editCancel() {
      this.edit = false;
    },

    handleDel(row) {
      this.$confirm("此操作将永久删除该条权限信息, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        this.$get($url.DATA_URL + "/gateway/delPermission?id=" + row.id).then(
          (res) => {
            if (res.data.code == 0) {
              this.$message({
                type: "success",
                message: "删除成功!",
              });
              this.searchFn();
            } else {
              this.$message({
                type: "error",
                message: res.data.msg,
              });
            }
          }
        );
      });
      // this.$delete( `/onlineteaching/user/${row.id}`).then((res) => {
      //   this.getUserList()
      // })
    },
    delDialog() {
      if (this.multipleSelection.length < 1) {
        return this.$message({
          type: "error",
          message: "请至少选择一条数据",
        });
      }
      let that = this;
      this.$confirm("此操作将永久删除数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        let ids = "";
        for (let i = 0; i < that.multipleSelection.length; i++) {
          if (i == that.multipleSelection.length - 1) {
            ids += that.multipleSelection[i].id.toString();
          } else {
            ids += that.multipleSelection[i].id.toString() + ",";
          }
        }
        this.$get($url.DATA_URL + "/gateway/delPermissions?ids=" + ids).then(
          (res) => {
            if (res.data.code == 0) {
              this.$message({
                type: "success",
                message: "删除成功!",
              });
              this.multipleSelection = [];
              this.searchFn();
            } else {
              this.$message({
                type: "error",
                message: res.data.msg,
              });
            }
          }
        );
      });
    },
    uploadFn() {
      this.templateShow = true;
    },
    uploadChange(file) {
      this.questionObject.file = file.raw;
    },
    uploadRemove() {
      this.file = null;
    },
    // 转换
    formatter(params) {
      let paramsArr = params.split("&");
      let paramsStr = "";
      for (let item in paramsArr) {
        paramsStr += encodeURIComponent(paramsArr[item].split("=")[0]) + "=";
        paramsStr += encodeURIComponent(paramsArr[item].split("=")[1]) + "&";
      }
      return paramsStr;
    },
    searchInputFun() {
      this.pageNum = 1;
      this.searchFn();
    },
    searchInputFun2() {
      this.pageNum2 = 1;
      this.searchFn2();
    },
    searchFn() {
      this.multipleSelection = [];
      if (!this.cookie.getCookie("username")) {
        this.$router.push("/login");
      }
      let params = {
        keywords: this.keywords,
        permissionInfoPage: {
          current: this.pageNum,
          size: this.pageSize,
        },
      };
      this.$post($url.DATA_URL + "/gateway/queryPermission", params).then(
        (res) => {
          if (res.data.code === 0) {
            this.tableData = res.data.data.details;
            this.total = res.data.data.total;
          } else {
            this.$message({
              type: "error",
              message: res.data.msg,
            });
            this.$router.push("/login");
          }
        }
      );
    },
    searchFn2() {
      if (!this.cookie.getCookie("username")) {
        this.$router.push("/login");
      }
      let params = {
        keywords: this.keywords2,
        permissionInfoPage: {
          current: this.pageNum2,
          size: this.pageSize2,
        },
      };
      this.$post($url.DATA_URL + "/gateway/queryPermission", params).then(
        (res) => {
          if (res.data.code === 0) {
            this.tableData2 = res.data.data.details;
            this.total = res.data.data.total;
          } else {
            this.$router.push("/login");
            // this.$message({
            //   type: 'error',
            //   message: res.data.msg
            // })
          }
        }
      );
    },
    resetFn() {
      this.username = "";
      this.tel = "";
      this.dataStatus = "";
      this.time = [];
      this.searchFn();
    },
    //按钮水波纹
    computeRippleStyles(element, event) {
      const { top, left } = element.getBoundingClientRect();
      const { clientWidth, clientHeight } = element;

      const radius = Math.sqrt(clientWidth ** 2 + clientHeight ** 2) / 2;
      const size = radius * 2;

      const localX = event.clientX - left;
      const localY = event.clientY - top;

      const centerX = (clientWidth - radius * 2) / 2;
      const centerY = (clientHeight - radius * 2) / 2;

      const x = localX - radius;
      const y = localY - radius;

      return { x, y, centerX, centerY, size };
    },
    createRipple(event) {
      const container = event.currentTarget;
      const { x, y, centerX, centerY, size } = this.computeRippleStyles(
        container,
        event
      );
      const ripple = document.createElement("div");
      ripple.classList.add("myripple");
      ripple.style.opacity = `0`;
      ripple.style.transform = `translate(${x}px, ${y}px) scale3d(.3, .3, .3)`;
      ripple.style.width = `${size}px`;
      ripple.style.height = `${size}px`;
      ripple.style.position = `absolute`;
      ripple.style.top = `0`;
      ripple.style.left = `0`;
      ripple.style["z-index"] = `100`;
      ripple.style["border-radius"] = `50%`;
      ripple.style["background-color"] = "#fad4a1"; //`currentColor`
      ripple.style["opacity"] = `0`;
      ripple.style.transition =
        "transform 0.2s cubic-bezier(0.68, 0.01, 0.62, 0.6)," +
        "opacity 0.08s linear";
      ripple.style["will-change"] = "transform, opacity";
      ripple.style["pointer-events"] = "none";
      // 记录水波的创建时间
      ripple.dataset.createdAt = String(performance.now());

      const { position } = window.getComputedStyle(container);
      container.style.overflow = "hidden";
      position === "static" && (this.style.position = "relative");

      container.appendChild(ripple);

      window.setTimeout(() => {
        ripple.style.transform = `translate(${centerX}px, ${centerY}px) scale3d(1, 1, 1)`;
        ripple.style.opacity = `.25`;
      });
    },
    removeRipple(event) {
      const container = event.currentTarget;
      const ripples = container.querySelectorAll(".myripple");
      if (!ripples.length) {
        return;
      }

      const lastRipple = ripples[ripples.length - 1];
      // 通过水波的创建时间计算出扩散动画还需要执行多久，确保每一个水波都完整的执行了扩散动画
      const delay =
        300 - performance.now() + Number(lastRipple.dataset.createdAt);

      setTimeout(() => {
        lastRipple.style.opacity = `0`;

        setTimeout(() => lastRipple.parentNode?.removeChild(lastRipple), 300);
      }, delay);
    },
  },
};
</script>

